.swiper-pagination-custom,
.swiper-pagination-fraction {
  width: auto;
  height: fit-content;
  right: 7.5em;
  left: auto;
  top: -7em;
}
@media (max-width: 1199px) {
  .swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
    right: 46%;
    top: auto;
    bottom: 5.4em;
  }
}
@media (max-width: 767px) {
  .swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
    right: 42.5%;
    top: auto;
    bottom: 2.3em;
  }
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0;
  margin-left: 15px;
}
@media (max-width: 767px) {
  .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
    margin-left: 12px;
  }
}

.swiper-pagination-bullet-active {
  background-color: #ffffff;
}

.swiper-buttons__back {
  background-color: #0266c1;
  border: none;
  padding: 11px 12px;
  cursor: pointer;
  outline: 1px solid transparent;
}
.swiper-buttons__back:hover, .swiper-buttons__back:focus-visible {
  outline: 1px solid #1c3374;
  background-color: transparent;
  transition: 0.3s;
}
.swiper-buttons__back:active {
  background-color: #1c3374;
}
.swiper-buttons__back:disabled {
  opacity: 0.1;
  pointer-events: none;
}

.swiper-buttons__next {
  background-color: #0266c1;
  border: none;
  transform: rotate(180deg);
  padding: 11px 12px;
  cursor: pointer;
  outline: 1px solid transparent;
}
.swiper-buttons__next:hover, .swiper-buttons__next:focus-visible {
  outline: 1px solid #1c3374;
  background-color: transparent;
  transition: 0.3s;
}
.swiper-buttons__next:active {
  background-color: #1c3374;
  transition: 0.3s;
}
.swiper-buttons__next:disabled {
  opacity: 0.1;
  pointer-events: none;
}

.swiper-buttons__back:hover .swiper-buttons__icon,
.swiper-buttons__next:hover .swiper-buttons__icon,
.swiper-buttons__back:focus-visible .swiper-buttons__icon,
.swiper-buttons__next:focus-visible .swiper-buttons__icon {
  fill: #1c3374;
  transition: 0.3s;
}

.swiper-buttons__back:active .swiper-buttons__icon,
.swiper-buttons__next:active .swiper-buttons__icon {
  fill: #ffffff;
  transition: 0.3s;
}

.swiper-buttons__icon {
  display: flex;
  align-self: center;
  fill: #ffffff;
}

